.Navigation {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2cm 1cm;
    border-bottom: thin solid rgb(174, 174, 174);
    position: relative;
    background-color: rgb(255, 255, 255);
}

.logo {
    width: 3cm;
    height: 3cm;
    cursor: pointer;
}

.logotext {
    width: 5cm;
    height: 1cm;
    cursor: pointer;
}

.logo-container {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}

.menu-container {
    display: flex;
    flex-direction: row;
    width: fit-content;
    align-items: center;
    column-gap: .5cm;
    width: fit-content;
}

.menu-items {
    display: flex;
    flex-direction: row;
    column-gap: 2px;
    align-items: center;
    width: fit-content;
}

.item {
    font-size: 16px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #000000;
    text-decoration: none;
}

.item:hover {
    text-decoration: underline;
    color: #ff00009d;
    font-weight: bold;
}



.About,
.Projects,
.Contact,
.Community {
    height: 1cm;
    width: 1cm;
}

img {
    background-color: transparent;
}

.active {
    color: #f459c8;
    font-weight: bold;
    text-decoration-line: underline;
}

.menu-icon {
    width: 30px;
    height: 30px;
    cursor: pointer;
    visibility: hidden;
}

@media only screen and (max-width: 625px) {
    .menu-icon {
        visibility: visible;
    }
    .menu-container {
        display: none;
    }
}

.overlay {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 0.5cm;
    height: 100%;
    width: 4cm;
    position: fixed;
    z-index: 1;
    background-color: rgb(255, 255, 255);
    top: 0;
    right: 0;
    overflow: hidden;
    transition: 1s ease-in-out;
    display: none;
}

.overlayItems {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    row-gap: 0.5cm;
    cursor: pointer;
    color: rgb(85, 85, 85);
}

.cls-btn {
    width: 0.8cm;
    height: 0.8cm;
    cursor: pointer;
    border-radius: 50%;
}


/* Responsive design */

@media only screen and (min-width: 1400px) {
    .logo {
        width: 3cm;
        height: 3cm;
        cursor: pointer;
    }

    .logotext {
        width: 5cm;
        height: 1cm;
        cursor: pointer;
    }

    .item {
        font-size: 16px;
    }

    .About,
    .Projects,
    .Contact,
    .Community {
        height: 1cm;
        width: 1cm;
    }

}

@media only screen and (min-width: 1050px) and (max-width: 1400px) {
    .logo {
        width: 2.5cm;
        height: 2.5cm;
        cursor: pointer;
    }

    .logotext {
        width: 5cm;
        height: 1cm;
        cursor: pointer;
    }

    .item {
        font-size: 15px;
    }

    .About,
    .Projects,
    .Contact,
    .Community {
        height: 0.8cm;
        width: 0.8cm;
    }
}


@media only screen and (min-width: 1050px) and (max-width: 1150px) {
    .logo {
        width: 2.25cm;
        height: 2.25cm;
        cursor: pointer;
    }

    .logotext {
        width: 5cm;
        height: 1cm;
        cursor: pointer;
    }

    .item {
        font-size: 13px;
    }

    .About,
    .Projects,
    .Contact,
    .Community {
        height: 0.7cm;
        width: 0.7cm;
    }
}

@media only screen and (min-width: 850px) and (max-width: 1050px) {
    .logo {
        width: 2cm;
        height: 2cm;
        cursor: pointer;
    }

    .logotext {
        width: 4cm;
        height: 0.8cm;
        cursor: pointer;
    }

    .item {
        font-size: 13px;
    }

    .About,
    .Projects,
    .Contact,
    .Community {
        height: 0.8cm;
        width: 0.8cm;
    }
}


@media only screen and (min-width: 650px) and (max-width: 850px) {
    .logo {
        width: 1.5cm;
        height: 1.5cm;
        cursor: pointer;
    }

    .logotext {
        display:none;
    }

    .item {
        font-size: 10px;
    }

    .About,
    .Projects,
    .Contact,
    .Community {
        height: 0.8cm;
        width: 0.8cm;
    }
    .Navigation{
        padding:0.2cm 0.8cm;
    }
}

@media only screen and (max-width: 650px) {
    .logo {
        width: 1.5cm;
        height: 1.5cm;
        cursor: pointer;
    }

    .logotext {
       display:none;
    }

    .item {
        font-size: 14px;
    }

    .About,
    .Projects,
    .Contact,
    .Community {
        height: 0.6cm;
        width: 0.6cm;
    }

    .Navigation{
        padding:0.2cm 0.5cm;
    }
    .overlay{
        display: none;
    }
}