.Projects {
    display: flex;
    flex-direction: row;
    width: fit-content;
    position: relative;
}

.divproject {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    overflow: scroll;
    column-gap: 0.75cm;
    row-gap: 0.8cm;
    /* padding:0.5cm; */
    overflow-y: scroll;
    overflow: hidden;
    flex-wrap: wrap;
    margin: 0.5cm;
    /* width: 100vw; */
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.2cm;
    column-gap: 0.4cm;
    cursor: pointer;
    width: fit-content;
    height: fit-content;
    border-radius: 3px;
    padding: 0.2cm 0.4cm;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border: thin solid rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
    position: relative;
    transition: .5s;
}

.card:hover {
    transform: scale(1.05);
}

.iframediv {
    border: thin solid grey;
}

.p-name {
    font-size: 20px;
    padding: 2px;
}

.codeyear {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 95%;
    align-items: center;
}

.code {
    border: thin solid grey;
    border-radius: 10px;
    padding: 0.05cm;
    color: rgb(31, 31, 119);
    cursor: pointer;
}

.year {
    color: green;
}

.bio {
    font-size: 12px;
    color: rgb(55, 55, 55)
}

@media only screen and (min-width: 650px) and (max-width: 850px) {
    .iframediv {
        height: 275px;
        width: 450px;
    }
}

@media only screen and (min-width:500px) and (max-width: 650px) {
    .iframediv {
        height: 250px;
        width: 400px;
    }

    .card {
        padding: 0.2cm;
    }

}

@media only screen and (min-width: 450px) and (max-width: 500px) {
    .iframediv {
        height: 250px;
        width: 350px;
    }

    .card {
        padding: 0.05cm;
    }

}

@media only screen and (min-width: 410px) and (max-width: 450px) {
    .iframediv {
        display: none !important;
    }

    .card {
        padding: 0.05cm;
        width: 80vw;
    }

    .divproject {
        width: 90vw;
    }

}

