* {
    margin: 0;
    padding: 0;
    /* background-color: rgb(255, 255, 255); */
}

.Home {
    height: 100%;
    width: 100%;
    overflow: hidden;
    background-color: transparent;
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    row-gap: 0.5cm;
}
.intro-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
}
.intro {
    display: flex;
    flex-direction: row;
    column-gap: 2cm;
    align-items: center;
    flex-wrap: nowrap;
    row-gap: 0.5cm;
    padding: 0.5cm;
    width: fit-content;
    
}

.para {
    font-family: Georgia, 'Times New Roman', Times, serif;
    display: flex;
    flex-direction: column;
   flex-wrap: wrap;
    row-gap: 0.2cm;
   
}

.para1 {
    font-size: 45px;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
   
}

.para1:hover {
    background: linear-gradient(to right, #a60643,
            #d05808, #e70fe7, #f6e765, rgb(162, 1, 255));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
}

.para2 {
    font-size: 18px;

}

.topic {
    border: 1px solid rgb(195, 175, 237);
    padding: 0.5cm;
    font-size: 20px;
    width: fit-content;
    border-radius: 0px 30px;
    margin: 0;
    /* z-index: 1; */
    background-color: rgb(211, 191, 243);
    box-shadow: rgba(56, 20, 58, 1) 0px 5px 15px;
}

.photo {
    padding: 0.5cm 0.5cm;
}

.p-photo {
    height: 10cm;
    width: 10cm;
    border-radius: 100%;
    padding: 0.5cm;
}

/* .aboutMeContainer{
    width:100vw;
} */
.aboutMe {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;

}

.timeline {
    padding: 0.2cm;
    margin: 0.5cm;
}
.timeline-block{
    display: flex;
    flex-direction: row;
    width: 100vw;
    align-items: center;
    justify-content: center;
}
.hr1 {
    width: 100%;
    margin: 0;

}

.left {
    display: flex;
    flex-direction: row;
    justify-content: left;
    margin-left: 1cm;
    margin-bottom: 0;
}


