.chart1 {   
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center; 
    overflow: scroll;
}

.progress {
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 0.5cm;
 
}

.calendar-container{
    width: 100vw;
}


@media only screen and (min-width: 1400px) {
    .para1 {
        font-size: 45px;
    }

    .para2 {
        font-size: 22px;
    }

    .p-photo {
        height: 11cm;
        width: 11cm;
    }

    .topic {
        font-size: 20px;
    }
}

