.aboutmeContainer {
  width: 100%;
  height:100%;
  padding: 0;
  margin: 0;
}

.AboutMe {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 1vw;
}

.introduction{
  width: 100%;
}

.topic{
  font-weight: bold;
}
.timeline-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100vw;
}

.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0.5cm;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 6px;
  background-color: rgb(85, 85, 85);
  top: 0;
  bottom: 0;
  margin-top: 0.2cm;
  /* left: 20%;  
   margin-left: -5px; */
}

.container {
  padding: 0.5cm 1cm;
  position: relative;
  background-color: inherit;
  width: fit-content;
}

.container::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -19px;
  background-color: rgb(228, 170, 224);
  border: 4px solid #ab56e8;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

.right {
  left: 1%;
}


.right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid rgb(79, 79, 79);
  border-width: 10px 10px 10px 0;
  border-color: transparent #ab56e8 transparent transparent;
}


.right::after {
  left: -16px;
}

.content {
  padding: 0.2cm 0.3cm;
  background-color: white;
  position: relative;
  border: 1px solid antiquewhite;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

@media screen and (max-width: 600px) {
  .timeline::after {
    left: 31px;
  }

  .container {
    /* width: 100%; */
    padding-left: 9px;
    /* padding-right: 5px; */
  }

  .right::before {
    left: 0px;
    border-color: transparent #ab56e8 transparent transparent;
  }

  .right::after {
    left: -1cm;
  }

  .right {
    left: 1cm;
  }
}

.about-year {
  font-size: 25px;
  font-weight: bold;
  color: rgba(1, 87, 208, 0.863)
}

.about-degree {
  font-size: 18px;
  color: #7f0fce
}

.about-school {
  font-size: 15px;
  color: rgb(41, 139, 41);
}

.about-board {
  font-size: 12px;
  color: rgb(185, 0, 0);

}

.skills-outline, .technologies-outline{
  display: flex;
  flex-direction: column;
  width: 100%;
  row-gap: 0.2cm;
}

.skills-container,
.techContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 0.5cm;
  row-gap: 0.5cm;
  padding: 0.5cm;
  margin: 3px;
  flex-wrap: wrap;

}

.skill,
.technology {
  border: thin solid antiquewhite;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 12px;
  padding: 5px;
  height: 2.5cm;
  width: 2.5cm;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 0.2cm;
  border-radius: 15px;
}

.skill:hover,
.technology:hover {
  transform: scale(1.1);
  transition: 0.5s;

}

.skillImg,
.techImg {
  height: 1.5cm;
  width: 1.5cm;

}

.p-image {
  height: 8cm;
  width: 8cm;
  border-radius: 50%;
}

.mainIntro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  column-gap: 0.3cm;
  row-gap: 0.3cm;
 width: 90vw;
}

.pic {
  padding-bottom: 0.5cm;
}

.p1 {
  font-size: 16px;
  /* border: 1px solid black; */
  padding: 0.5cm;
  border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

}

.technologies,
.skills {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* padding:0.5cm; */
}



