.MainComp{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 0.5cm;
    padding:1cm;
    margin: 1cm;
    position: relative;
}

.iframecss{
    height:10cm;
    width:25cm;
    border:0.5px solid rgb(255, 255, 255);
    border-radius: 12px;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px 5px;
}

.tapButton{
    padding: 0.75cm;
    border:1px solid rgb(57, 143, 217);
    color:white;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px 5px;
    background-color:rgb(57, 146, 224);
    border-radius: 12px;
    font-size: 16px;
}

.tapButton:hover{
    transform: scale(1.1);
    transition:1s;
    background-color: rgb(19, 68, 182);
}